export default{
	data(){
		return{
			userInfo:'',
			operateList:[
				{image:'../../../static/img/mine/icon-shezhi@2x.png',index:0,text:'基本设置'},
				{image:'../../../static/img/mine/icon-xiugaimima@2x.png',index:1,text:'修改密码'},
				{image:'../../../static/img/mine/icon-fankui@2x.png',index:2,text:'意见反馈'},
			]
		}
	},
	methods:{
		showWorkClick(){
			uni.navigateTo({
				url:'../work/work'
			})
		},
		showMessageClick(){
			uni.navigateTo({
				url:'../message/message'
			})
		},
		showIndexClick(){
			uni.navigateTo({
				url:'../index/index'
			})
		},
		getUserInfo(){
			uni.getStorage({
				key:'userInfo',
				success: (res) => {
					this.userInfo=res.data;
					console.log(this.userInfo,'个人也买你')
				}
			})
		},
		operateItem(index){

			switch(index){
				case 0:
				//基本设置
				uni.navigateTo({
					url:'../../tab-item-content/mine-center/basic-setting/basic-setting'
				})
				break;
				case 1:
				//修改密码
				uni.navigateTo({
					url:'../../tab-item-content/mine-center/edit-password/edit-password'
				})
				break;
				case 2:
				//意见反馈
				uni.navigateTo({
					url:'../../tab-item-content/mine-center/suggestion-list/suggestion-list'
				})
				break;
			}
		}
	},
	onLoad(){
		this.getUserInfo()
		
	}
}
